.home{
  background-image: url("https://images.unsplash.com/photo-1518754630090-25731934bfdd?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=891&q=80");
  /* background-image: url("./components/img/homeBg.jpg"); */
  background-size: cover;
  height: 100vh;
  background-repeat: no-repeat;
  /*clip-path: polygon(0 0, 100% 0, 100% 87%, 0 100%);*/
  position: relative;
  background-position: center;
}
.dark-overlay{
  opacity: 0.6;
  /* background-color: rgb(214, 32, 32);
  color: #111;
  z-index: 555; */
}
.card-image{
  background-image: url("https://images.unsplash.com/photo-1531170515419-b26209bbe710?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=775&q=80");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
}
.card{
  background-color: #6C7A89;
}

.card-body{
  color: white;
}
.btn-home a{
  color: #fff !important;
  font-weight: bold;
}
.counter-section i{
  background-color: #5D3F6A;
  padding: 15px;
  border-radius: 50%;
  margin-top: 15px;
  font-size: 20px;
}
.counter-section i:hover{
  transform: rotate(360deg);
  background-color: #6C7A89;
  color: #fff;
  /* Safari */
  -webkit-transform: rotate(-90deg);

  /* Firefox */
  -moz-transform: rotate(-180deg);

  /* IE */
  -ms-transform: rotate(-90deg);

  /* Opera */
  -o-transform: rotate(-90deg);
  

}
.h3-tag h3{
  color: #5B3256;
}
.contact{
  /* background-image: url("https://images.unsplash.com/photo-1576983161955-044b3f32d944?ixlib=rb-1.2.1&auto=format&fit=crop&w=1608&q=80"); */
  background-image: url("https://images.unsplash.com/photo-1525915818695-5b1a033247f1?ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80");
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  color: #fff;
}
.dark-overlay {
  background: rgb(93, 63, 106, 0.9);
  min-height: 700px; }
.bg-dark{
  opacity: 0.6;
  background: #000 !important;
  color: #111;
}
.navbar a{
  color: rgb(172, 175, 177) !important;
}
.nav-item a{
  color: aliceblue !important;
}
.header{
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#footer-section{
  background-image: url("./components/img/footer.jpg");
  height: 55vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.footer a{
  border: 3px solid #fff;
  margin: 5px;
  border-radius: 10px 0 10px 0;
}
.footer a:hover{
  transform:rotate(360deg);
  -ms-transform:rotate(360deg);
  -webkit-transform:rotate(360deg);
  transform: translateY(10px);
  background-color: #5B3256;
  transition: all ease .6s;
}
.address{
  position: relative;
  margin-top: -95px;
  color: #fff;
}
.address p{
  margin: 0;
  padding: 0;
}
.menu_active {
  font-weight: bold;
  border-bottom: 3px solid #565387;
}
.con-info{
  font-size: 14px;
}
.enrollCourse{
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;

}
.enrollName{
  color: orange;
}